.plyr-prev {
    margin-right: 0 !important;
    position: relative;
    display: inline-block;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    overflow: visible;
    vertical-align: middle;
    padding: 7px;
    border: 0;
    background: 0 0;
    border-radius: 3px;
    cursor: pointer;
    transition: background .3s ease, color .3s ease, opacity .3s ease;
    color: inherit;
}

.plyr-next {
    margin-left: 0 !important;
    position: relative;
    display: inline-block;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    overflow: visible;
    vertical-align: middle;
    padding: 7px;
    border: 0;
    background: 0 0;
    border-radius: 3px;
    cursor: pointer;
    transition: background .3s ease, color .3s ease, opacity .3s ease;
    color: inherit;
}

.plyr__controls [data-plyr="play"] {
    margin-left: 0 !important;
}

.anime-video{
    width: 70vw;
}

@media screen and (max-width: 600px) {
    .anime-video{
        width: 100vw;
    }
}