.search-icon {
    color: white;
}

.search-box svg {
    font-size: 2.2rem;
}

.search-box input {
    color: white;
    background-color: rgb(5, 5, 5);
    border: none;
    border-bottom: 1px solid snow;
    padding: 5px;
    width: 800px;
    height: 25px;
    font-size: 1.5rem;
}

.search-box input:focus {
    outline: none;
}

.search-box {
    border: 2px solid rgb(86, 39, 134);
    border-radius: 30px;
    height: 50px;
    width: fit-content;
    padding: 5px 15px;
    display: flex;
    align-items: center;
}

.search-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media screen and (max-width: 1600px) and (min-width: 1200px) {
    .search-box input {
        width: 800px;
    }
}

@media screen and (max-width: 1200px) and (min-width: 750px) {
    .search-box input {
        width: 600px;
    }
}

@media screen and (max-width: 750px) and (min-width: 450px){
    .search-box input {
        width: 350px;
    }
}
@media screen and (max-width: 450px) {
    .search-box input {
        width: 200px;
    }
}