.navbar,
.navbar-mobile {
    display: flex;
    width: 100vw;
    position: sticky;
    height: 5rem;
    background-color: rgb(5, 5, 5);
    align-items: center;
    justify-content: space-between;
}

.navbar-mobile {
    display: none;
}


.navButtons {
    list-style-type: none;
    display: flex;
    padding: 25px;
    font-size: 1.2rem;
}

.navButtons a,
.menu a {
    padding: 10px;
    text-decoration: none;
    color: white;
}

#animeName {
    position: absolute;
    right: 7.5rem;
    margin: 0 5px;
    width: 25rem;
    font-size: 1.2rem;
    border: none;
    background-color: rgb(5, 5, 5);
    border-bottom: 1px solid white;
    color: white;
    z-index: 1;
}

#animeName:focus {
    outline: none;
}

.search-result {
    position: absolute;
    top: 3rem;
    right: 7.5rem;
    padding: 10px 5px;
    width: 25rem;
    font-size: 1.2rem;
    background-color: rgb(5, 5, 5);
    color: white;
}

.logo-img {
    height: 2rem;
    padding: 25px;
}

.user-account,
.menu {
    color: white;
    cursor: pointer;
    font-size: 1.2rem;
}

.account {
    padding-left: 10px;
}

.menu-button {
    color: white;
    padding: 15px;
}

.menu {
    display: flex;
    flex-direction: column;
    row-gap: 15px;
    list-style-type: none;
    position: absolute;
    background-color: rgb(5, 5, 5);
    top: 3.5rem;
    right: 0;
    height: calc(100vh - 4rem);
    padding: 10px 5px;
    padding-left: 15px;
    width: 10rem;
    font-size: 1.2rem;
    color: white;
}

.search-result {
    max-height: 80vh;
    overflow-y: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(222, 222, 222, 0.3); 
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(222, 222, 222, 0.5); 
}

.fancybox {
    display: flex;
    padding: 5px;
}

.fancybox img{
    padding: 5px;
    padding-right: 15px;
    height: 70px;
}

.fancybox:hover {
    background-color: rgba(51,51,51,.7);
    border-radius: 5px;
}

@media screen and (max-width: 600px) {
    .navbar {
        display: none;
    }

    .navbar-mobile {
        display: flex;
    }
}