.anime-details{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
}

.details *{
    padding-left: 5px;
    padding-bottom: 5px;
    display: inline;
}

.common-details{
    padding: 50px;
    display: flex;
    column-gap: 50px;
}

.all-episodes{
    display: grid;
    width:80%;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    padding:10px 20px;
    column-gap: 10%;
    row-gap: 10px;
}

.all-episodes div{
    border: 2px solid rgb(86, 39, 134);
    border-radius: 25px;
    height: 20px;
    padding: 5px 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(5, 5, 5);
    transition: 1s;
}

.all-episodes:hover  div{
    transform: scale(.9);
}

.all-episodes  div:hover{
    transform: scale(1.15);
    background-color: rgba(51,51,51,1);
}

@media screen and (max-width: 800px) {
    .common-details{
        padding: 20px;
        align-items: left;
        flex-direction: column;
    }

    img{
        width: 80%;
    }

}